import { NgModule } from '@angular/core';

import { UsersComponent } from './users.component';

import { FullNamePipe } from './shared/full-name.pipe';
import { UsersResolver} from './shared/users-resolver.service';

@NgModule({
    imports: [
        NgModule
    ],
    declarations: [
        UsersComponent,
        FullNamePipe
    ],
    providers: [
        UsersResolver
    ]
})
export class UserModule { }


